 40px;\n    }\n}",".todo-list {\n\n    &__buttons {\n        .btn{\n            &:not(:first-of-type){\n                margin-left: 5px;\n            }\n        }\n    }\n}",".navigation {\n    display: flex;\n    justify-content: space-between;\n    color: $font-on-primary;\n    background-color: $primary-variant-color;\n    &__checkbox {\n        display: none;\n    }\n    \n    &__button {\n        display: none;\n        @include responsive(phone) {\n            display: block;\n            background-color: #ffffff;\n            height: 70px;\n            width: 70px; \n            position: fixed;\n            top: 50px;\n            right: 50px;\n            border-radius: 50%;\n            z-index: 10;\n            box-shadow: 0 10px 30px rgba(#000, .3);\n            text-align: center;\n            cursor: pointer;\n        }\n    }\n    \n    &__background {\n        display: none;\n        @include responsive(phone) {\n            display: block;\n            height: 60px;\n            width: 60px;\n            border-radius: 50%;\n            position: fixed;\n            top: 55px;\n            right: 55px;\n            background-image: radial-gradient(lighten(blue, 20%), darken(blue, 10%));\n            z-index: 5;\n            transition: all .3s ease-out;        \n        }\n    }\n\n    &__brand {\n        display: flex;\n        font-size: 2em;\n        align-items: center;\n        padding: 0 20px;\n        @include responsive(phone){\n            display: none;\n        }\n    }\n    &__brand &__link:link,\n    &__brand &__link:visited {\n        color: $font-on-primary;\n    }\n    \n    &__nav{\n        display: flex;\n        position: relative;\n        @include responsive(phone){\n            height: 100vh;\n            width: 100%;\n            position: fixed;\n            top: 0;\n            right: 0;\n            z-index: 8;\n            opacity: 0;\n            width: 0;\n            transition: all .3s;\n        }\n    }\n    \n    &__list{\n        display: flex;\n        flex-direction: row;\n        justify-content: flex-end;\n        position: relative;\n        padding: 10px 20px;\n        margin-bottom: 0;\n        @include responsive(phone) {\n            position: absolute;\n            top: 50%;\n            left: 50%;\n            transform: translate(-50%, -50%);\n            text-align: center;\n            list-style: none;\n            width: 100%;\n        }\n    }\n    \n    &__item{\n        margin: 10px;\n        display: inline-block;\n        @include responsive(phone) {\n            display: block;\n        }\n    }\n    \n    &__link {\n        &:link,\n        &:visited{\n            color: #000;\n            display: inline-block;\n            font-weight: 300;\n            text-decoration: none;\n            @include responsive(phone) {\n                color: #FFF;\n                font-size: 30px;\n                padding: 10px 20px;\n                text-decoration: none;\n                text-transform: uppercase;\n                background-image: linear-gradient(120deg, transparent 0%, transparent 50%, white 50%);\n                background-size: 230%;\n                transition: all .3s;\n            }\n        }\n        \n        &:hover,\n        &:active {\n            color: blue;\n            transform: translateX(1px) translateY(-1px);\n            box-shadow: 0 0 5px 1px rgba(#000, .1);\n            transition: all .2s;\n            @include responsive(phone){\n                background-position: 100%;\n                color: green;\n                transform: translateX(10px);\n            }\n        }\n    }\n    \n    // functionality\n    &__checkbox:checked ~ &__background {\n        transform: scale(80);\n    }\n    &__checkbox:checked ~ &__nav {\n        opacity: 1;\n        width: 100%;\n    }\n    \n    // icon\n    &__icon{\n        position: relative;\n        margin-top: 35px;\n        &,\n        &::before,\n        &::after {\n            width: 30px;\n            height: 2px;\n            background-color: #333;\n            display: inline-block;\n        }\n        &::before,\n        &::after {\n            content: \"\";\n            position: absolute;\n            left: 0;\n            transition: all .3s;\n        }\n       \n        &::before {top: 8px;}\n        &::after {top: -8px;}\n    }\n    \n    &__button:hover &__icon::before {top: 10px}\n    &__button:hover &__icon::after {top: -10px}\n    \n    &__checkbox:checked +&__button &__icon {\n        background-color: transparent;\n        &::before{\n            top: 0;\n            transform: rotate(135deg);\n        }\n        &::after {\n            top: 0;\n            transform: rotate(-135deg);\n        }\n    }\n}\n","@mixin responsive($breakpoint) {\n    @if $breakpoint == phone {\n      @media (max-width: 767.98px) { @content };\n    }\n    @if $breakpoint == tablet {\n      @media (max-width: 1365.98px){ @content };\n    }\n    @if $breakpoint == desktop {\n      @media (min-width: 1366px){ @content };\n    }\n}","h2.section-header{\n    letter-spacing: .1rem;\n    font-weight: normal;\n    text-transform: uppercase;\n    font-size: 24px;\n    padding: 40px 0;\n}",".todo-list-form {\n\n    .btn{\n        overflow: hidden;\n        &-secondary {\n            margin-left: 5px;\n        }\n    }\n}"]}]);

